import React, { useCallback } from 'react';

import { Link as RouteLink } from 'react-router-dom';

import { 
	Dialog,
	DialogTitle,
	DialogActions,
	DialogContent,
	DialogContentText,
	Button,
	Link,
} from '@material-ui/core';

// Components
import AnimationDialog from '../../components/AnimationDialog';
import converterCursoCode from '../../components/funciones/converterCursoCode';

// Redux
import { useSelector, useDispatch } from 'react-redux';
import updateDialogs from '../../actions/updateDialogs';

export default function ShowRegistros() {
	const { open, data } = useSelector((state) => ({
		open: state.dialogs.showRegistros.open,
		data: state.dialogs.showRegistros.data,
	}));
	const dispatch = useDispatch();

	const handleClose = () => {
		dispatch(updateDialogs('showRegistros', false, false));
	};
	
	const Text = useCallback(() => {
		if (data.action === 'Inicio de sesin por relogin') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) entr al sistema mediante su llave de relogin.
				</DialogContentText>
			);
		}else if (data.action === 'Inicio de sesin') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) entr al sistema mediante el formulario del login.
				</DialogContentText>
			);
		}else if (data.action === 'Bloqueo de cuenta') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) bloque su cuenta, teniendo un bloqueo de <strong>nivel {data.payload.nivel}</strong> y esperando <strong>{data.payload.time_block} minutos</strong> para su desbloqueo.
				</DialogContentText>
			);
		}else if (data.action === 'Sesin cerrada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) cerr su sesin dentro del sistema.
				</DialogContentText>
			);
		}else if (data.action === 'Sesiones cerradas') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) cerr todas sus sesiones (<strong>{data.payload.tokens}</strong>) dentro del sistema.
				</DialogContentText>
			);
		}else if (data.action === 'Correo de recuperacin') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) solicit un correo de recuperacin para poder recuperar su contrasea.
				</DialogContentText>
			);
		}else if (data.action === 'Contrasea cambiada via correo') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) cambi su contrasea mediante la verificacin del correo.
				</DialogContentText>
			);
		}else if (data.action === 'Publicacin creada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) cre una publicacin con el ttulo <Link component={RouteLink} to={data.payload.url}>{data.payload.title}</Link>.
				</DialogContentText>
			);
		}else if (data.action === 'Publicacin editada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) edit la publicacin <Link component={RouteLink} to={data.payload.url}>{data.payload.title}</Link>.
				</DialogContentText>
			);
		}else if (data.action === 'Publicacin eliminada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) se elimin la publicacin <strong>{data.payload.title}</strong>.
				</DialogContentText>
			);
		}else if (data.action === 'Usuario creado') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) cre a un usuario con los siguientes datos:
					<br />
					<br />
					<strong>Usuario:</strong> {data.payload.privilegio}{data.payload.username}.
					<br />
					<strong>Nombre:</strong> {data.payload.name}.
					<br />
					<strong>Correo:</strong> {data.payload.email}.
				</DialogContentText>
			);
		}else if (data.action === 'Usuario editado') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) edit al usuario <strong>{data.payload.privilegio}{data.payload.username}</strong>.
				</DialogContentText>
			);
		}else if (data.action === 'Usuario desactivado') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) desactiv al usuario con los siguientes datos:
					<br />
					<br />
					<strong>Usuario:</strong> {data.payload.privilegio}{data.payload.username}.
					<br />
					<strong>Nombre:</strong> {data.payload.name}.
					<br />
					<strong>Correo:</strong> {data.payload.email}.
				</DialogContentText>
			);
		}else if (data.action === 'Usuarios desactivados masivamente') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) desactiv <strong>{data.payload.users_disabled_count}</strong> usuarios, las cuales son las siguientes:
					<br />
					<br />
					{data.payload.users_disabled?.map((username, i) => (
						<React.Fragment>
							- {username}
							<br />
						</React.Fragment>
					))}
				</DialogContentText>
			);
		}else if (data.action === 'Actualizacin de seccin masiva') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) cambi el curso a <strong>{data.payload.curso}</strong> de <strong>{data.payload.massive_seccion_update_count}</strong> estudiante(s), los cuales son las siguientes:
					<br />
					<br />
					{data.payload.massive_seccion_update?.map((username, i) => (
						<React.Fragment>
							- {username}
							<br />
						</React.Fragment>
					))}
				</DialogContentText>
			);
		}else if (data.action === 'Matrcula cargada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) carg la matrcula del curso <strong>{data.payload.curso}</strong> cargando <strong>{data.payload.studiends_count}</strong> estudiante(s), los cuales son las siguientes:
					<br />
					<br />
					{data.payload.studiends?.map((username, i) => (
						<React.Fragment>
							- {username}
							<br />
						</React.Fragment>
					))}
				</DialogContentText>
			);
		}else if (data.action === 'Usuario restaurado') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) restaur al siguiente usuario:
					<br />
					<br />
					<strong>Usuario:</strong> {data.payload.privilegio}{data.payload.username}.
					<br />
					<strong>Nombre:</strong> {data.payload.name}.
					<br />
					<strong>Correo:</strong> {data.payload.email}.
				</DialogContentText>
			);
		}else if (data.action === 'Usuarios restaurados masivamente') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) restaur <strong>{data.payload.users_restored_count}</strong> usuarios, los cuales son los siguiente:
					<br />
					<br />
					{data.payload.users_restored?.map((username, i) => (
						<React.Fragment>
							- {username}
							<br />
						</React.Fragment>
					))}
				</DialogContentText>
			);
		}else if (data.action === 'Usuario eliminado') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) elimin al siguiente usuario del sistema:
					<br />
					<br />
					<strong>Usuario:</strong> {data.payload.privilegio}{data.payload.username}.
					<br />
					<strong>Nombre:</strong> {data.payload.name}.
					<br />
					<strong>Correo:</strong> {data.payload.email}.
				</DialogContentText>
			);
		}else if (data.action === 'Usuarios eliminados masivamente') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) elimin <strong>{data.payload.users_destroy_count}</strong> usuarios, los cuales son los siguiente:
					<br />
					<br />
					{data.payload.users_destroy?.map((username, i) => (
						<React.Fragment>
							- {username}
							<br />
						</React.Fragment>
					))}
				</DialogContentText>
			);
		}else if (data.action === 'Carga de matricula') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) subi una matrcula al servidor y esta fue procesada.
				</DialogContentText>
			);
		}else if (data.action === 'Curso creado') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) cre el curso <strong>{data.payload.curso}</strong>.
				</DialogContentText>
			);
		}else if (data.action === 'Curso eliminado') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) elimin el curso <strong>{data.payload.curso}</strong>, borrando <strong>{data.payload.boletas}</strong> boletas del sistema.
				</DialogContentText>
			);
		}else if (data.action === 'Cursos eliminados masivamente') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) elimin <strong>{data.payload.cursos}</strong> cursos, los cuales fueron los siguientes:
					<br />
					<br />
					{data.payload.names?.map((code, i) => (
						<React.Fragment>
							- {code}
							<br />
						</React.Fragment>
					))}
				</DialogContentText>
			);
		}else if (data.action === 'Boletas cargadas') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) carg <strong>{data.payload.boletas}</strong> boletas al sistema.
				</DialogContentText>
			);
		}else if (data.action === 'Boleta editada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) edit la boleta <strong>{converterCursoCode(data.payload.curso)} {data.payload.seccion} - {data.payload.lapso} Lapso</strong> del estudiante <strong>{data.payload.name}</strong> ({data.payload.username}).
				</DialogContentText>
			);
		}else if (data.action === 'Boleta eliminada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) elimin la boleta <strong>{converterCursoCode(data.payload.curso)} {data.payload.seccion} - {data.payload.lapso} Lapso</strong> del estudiante <strong>{data.payload.name}</strong> ({data.payload.username}).
				</DialogContentText>
			);
		}else if (data.action === 'Boletas eliminadas masivamente') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) elimin <strong>{data.payload.boletas}</strong> del sistema.
				</DialogContentText>
			);
		}else if (data.action === 'Solicitud de contcto eliminada') {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) elimin la solicitud de contcto <strong>{data.payload.asunto}</strong>, la cual fue escrita por <strong>{data.payload.nombre}</strong> ({data.payload.email}).
				</DialogContentText>
			);
		}else {
			return (
				<DialogContentText>
					El da <strong>{data.date}</strong> a las <strong>{data.hours}</strong> el usuario <strong>{data.name}</strong> ({data.username}) se realiz esta accin.
				</DialogContentText>
			);
		}
	},[data]);
	
	return (
		<Dialog 
			open={open}
			TransitionComponent={AnimationDialog}
			aria-labelledby="show-registros"
			aria-describedby="show-registros-describedby"
			onClose={handleClose}
		>
			<DialogTitle id="confirm-dialog-title">Registro #{data.id}</DialogTitle>
			<DialogContent>
				<Text />
			</DialogContent>
			<DialogActions>
				<Button
					onClick={handleClose}
				>
					Entendido
				</Button>
			</DialogActions>
		</Dialog>
	);
}